.result-display {
  width: 100%;
  height: 520px;
  padding-bottom: 20px;
  padding-left: 30px;
}

.scrollarea {
  width: 100%;
  height: 100%;
  padding-right: 20px;
}

.result-summary {
  display: flex;
  height: 50px;
  width: 100%;
  font-size: 0.85em;
  padding-left: 10px;
  justify-content: center;
  text-align: center;
}

.result-summary > a {
  margin-top: -1px;
}

.result-summary > p {
  margin-left: 5px;
}

.pagination-container {
  position: relative;
  height: 60px;
}

.pagination-component {
  position: absolute;
  bottom: 0px;
}

.list-group-item {
  font-size: 0.85em;
}

.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
}

.spinner-box {
  vertical-align: middle;
  margin-top: 170px;
}

h5 svg{
  margin-right: 10px;
}

video{
  height: 100%;
  width: 100%;
}
